// src/license.js
// Lemon Squeezy license activation + validation helpers.

import { getLicense, setLicense, clearLicense } from "./shared/core";

const LS_ACTIVATE_ENDPOINT = "https://api.lemonsqueezy.com/v1/licenses/activate";
const LS_VALIDATE_ENDPOINT = "https://api.lemonsqueezy.com/v1/licenses/validate";
const INSTANCE_NAME = "Neurow Add-in";
const MIN_LICENSE_LENGTH = 5;

function normalizeKey(key) {
  return String(key || "").trim();
}

function normalizeExpiresAt(value) {
  const raw = String(value || "").trim();
  if (!raw) return "";
  const ts = Date.parse(raw);
  if (!Number.isFinite(ts)) return "";
  return new Date(ts).toISOString();
}

function extractError(data, fallback) {
  if (!data) return fallback;
  if (typeof data.error === "string" && data.error.trim()) return data.error.trim();
  if (typeof data.message === "string" && data.message.trim()) return data.message.trim();
  if (Array.isArray(data.errors) && data.errors.length) {
    const first = data.errors[0] || {};
    const detail = first.detail || first.title || first.message;
    if (detail) return String(detail).trim();
  }
  return fallback;
}

async function postLicense(endpoint, payload) {
  const formData = new FormData();
  Object.entries(payload || {}).forEach(([key, value]) => {
    if (value == null || value === "") return;
    formData.append(key, value);
  });

  const response = await fetch(endpoint, { method: "POST", body: formData });
  let data = null;
  try {
    data = await response.json();
  } catch {
    data = null;
  }
  return { ok: response.ok, status: response.status, data };
}

function buildLicense(key, data, overrides = {}) {
  const instanceId = overrides.instanceId || data?.instance?.id || data?.instance_id || "";
  const instanceName = data?.instance?.name || overrides.instanceName || INSTANCE_NAME;
  const expiresAt = normalizeExpiresAt(data?.license_key?.expires_at || overrides.expiresAt);
  const now = new Date().toISOString();

  return {
    key,
    instanceId,
    instanceName,
    expiresAt,
    valid: true,
    activatedAt: overrides.activatedAt || now,
    lastCheckedAt: now
  };
}

function normalizeLicense(raw) {
  if (!raw || typeof raw !== "object") return null;
  const key = normalizeKey(raw.key);
  if (!key) return null;

  return {
    key,
    instanceId: String(raw.instanceId || "").trim(),
    instanceName: String(raw.instanceName || INSTANCE_NAME).trim(),
    expiresAt: normalizeExpiresAt(raw.expiresAt),
    valid: raw.valid !== false,
    activatedAt: String(raw.activatedAt || "").trim(),
    lastCheckedAt: String(raw.lastCheckedAt || "").trim()
  };
}

export async function getStoredLicense() {
  const stored = await getLicense();
  return normalizeLicense(stored);
}

export async function saveStoredLicense(license) {
  const normalized = normalizeLicense(license);
  if (!normalized) {
    await clearLicense();
    return null;
  }
  await setLicense(normalized);
  return normalized;
}

export async function clearStoredLicense() {
  await clearLicense();
}

export function isLicenseValid(license) {
  if (!license || !license.key) return false;
  if (license.valid === false) return false;
  if (license.expiresAt) {
    const ts = Date.parse(license.expiresAt);
    if (Number.isFinite(ts) && ts <= Date.now()) return false;
  }
  return true;
}

export async function hasValidLicense() {
  const stored = await getStoredLicense();
  return isLicenseValid(stored);
}

export async function activateLicense(key, options = {}) {
  const trimmed = normalizeKey(key);
  if (!trimmed || trimmed.length < MIN_LICENSE_LENGTH) {
    return { success: false, error: "Cle trop courte" };
  }

  const stored = await getStoredLicense();
  if (!options.force && stored && stored.key === trimmed && isLicenseValid(stored)) {
    return { success: true, license: stored, cached: true };
  }

  try {
    const { ok, status, data } = await postLicense(LS_ACTIVATE_ENDPOINT, {
      license_key: trimmed,
      instance_name: INSTANCE_NAME
    });

    if (!ok) {
      return { success: false, error: extractError(data, `Erreur API (${status})`) };
    }

    if (data?.activated) {
      const license = buildLicense(trimmed, data, { activatedAt: stored?.activatedAt });
      await setLicense(license);
      return { success: true, license };
    }

    return { success: false, error: extractError(data, "Cle invalide") };
  } catch {
    return { success: false, error: "Erreur de connexion internet" };
  }
}

export async function validateLicense(key, options = {}) {
  const trimmed = normalizeKey(key);
  if (!trimmed || trimmed.length < MIN_LICENSE_LENGTH) {
    return { success: false, error: "Cle trop courte" };
  }

  const stored = await getStoredLicense();
  const instanceId = options.instanceId || stored?.instanceId || "";

  try {
    const { ok, status, data } = await postLicense(LS_VALIDATE_ENDPOINT, {
      license_key: trimmed,
      instance_id: instanceId || undefined
    });

    if (!ok) {
      return { success: false, error: extractError(data, `Erreur API (${status})`) };
    }

    if (data?.valid) {
      const license = buildLicense(trimmed, data, { instanceId, activatedAt: stored?.activatedAt });
      return { success: true, license };
    }

    return { success: false, error: extractError(data, "Cle invalide") };
  } catch {
    return { success: false, error: "Erreur de connexion internet" };
  }
}
