function el(id) {
  return document.getElementById(id);
}

function setStatus(line, detailsObj) {
  const safeLine = redactSensitiveText(line || "");
  el("statusLine").textContent = safeLine;
  if (detailsObj) {
    try {
      const safeDetails = redactSensitiveValue(detailsObj);
      el("statusDetails").textContent = JSON.stringify(safeDetails, null, 2);
    } catch {
      el("statusDetails").textContent = redactSensitiveText(detailsObj);
    }
  } else {
    el("statusDetails").textContent = "";
  }
}

function showToast(message, options = {}) {
  const { duration = 2600 } = options;
  const toast = el("statusToast");
  if (!toast) return;
  toast.textContent = message || "";
  toast.classList.add("is-visible");
  if (toastTimer) {
    clearTimeout(toastTimer);
    toastTimer = null;
  }
  toastTimer = setTimeout(() => {
    toast.classList.remove("is-visible");
  }, duration);
}

function formatNumber(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return "0";
  return Math.round(n).toLocaleString("en-US");
}

const CURRENCY_SYMBOL = "";
const COST_ICON = "$";
const REASON_ICON = "\uD83E\uDDE0";
const TOKEN_IN_ICON = "IN";
const TOKEN_REASON_ICON = "REF";
const TOKEN_OUT_ICON = "OUT";
const TOKEN_CACHE_ICON = "CACHE";
const LICENSE_PLACEHOLDER = "AAAA-BBBB-CCCC-DDDD";

const FAVORITE_ICON = "\u2605";

function formatCost(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return `${CURRENCY_SYMBOL}0.0000`;
  return `${CURRENCY_SYMBOL}${n.toFixed(4)}`;
}

function formatCostTotal(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return `0,00${CURRENCY_SYMBOL}`;
  return `${n.toFixed(2).replace(".", ",")}${CURRENCY_SYMBOL}`;
}

function formatCostCents(value) {
  const n = Number(value);
  if (!Number.isFinite(n) || n <= 0) return "00c";
  const cents = Math.max(0, Math.round(n * 100));
  return `${String(cents).padStart(2, "0")}c`;
}

function isGeminiProvider(value) {
  return String(value || "").toLowerCase() === PROVIDERS.GEMINI;
}

function isCacheAdjustedEntry(entry) {
  return !!entry?.cacheAdjusted || entry?.fn === "AI.BATCH";
}

function shouldShowCacheTokens(entry) {
  const cached = Number(entry?.cachedInputTokens || 0);
  if (!Number.isFinite(cached) || cached <= 0) return false;
  return entry?.fn === "AI.BATCH" || isGeminiProvider(entry?.provider);
}

function getDisplayInputTokens(entry) {
  const inputTokens = Math.max(0, Number(entry?.inputTokens || 0));
  if (!shouldShowCacheTokens(entry)) return inputTokens;
  if (isCacheAdjustedEntry(entry)) return inputTokens;
  const cachedInputTokens = Math.max(0, Number(entry?.cachedInputTokens || 0));
  return Math.max(0, inputTokens - cachedInputTokens);
}

function getDisplayTokensTotal(entry) {
  const inputTokens = getDisplayInputTokens(entry);
  const reasoningTokens = Math.max(0, Number(entry?.reasoningTokens || 0));
  const outputTokens = Math.max(0, Number(entry?.outputTokens || 0));
  return inputTokens + reasoningTokens + outputTokens;
}

function splitInputTokensForCost(entry) {
  const inputTokens = Math.max(0, Number(entry?.inputTokens || 0));
  const cachedInputTokens = Math.max(0, Number(entry?.cachedInputTokens || 0));
  if (isCacheAdjustedEntry(entry)) {
    return { uncachedInputTokens: inputTokens, cachedInputTokens };
  }
  const cached = Math.min(cachedInputTokens, inputTokens);
  return { uncachedInputTokens: Math.max(0, inputTokens - cached), cachedInputTokens: cached };
}

function formatDuration(ms) {
  const n = Number(ms);
  if (!Number.isFinite(n)) return "0.00s";
  return `${(n / 1000).toFixed(2)}s`;
}

function formatTime(ts) {
  try {
    const d = new Date(ts);
    return d.toLocaleTimeString("fr-FR", { hour: "2-digit", minute: "2-digit" });
  } catch {
    return "--:--";
  }
}

function formatDate(ts) {
  try {
    const d = new Date(ts);
    return d.toLocaleDateString("fr-FR", { year: "numeric", month: "short", day: "2-digit" });
  } catch {
    return "";
  }
}

function formatDateTime(ts) {
  try {
    const d = new Date(ts);
    return d.toLocaleString("fr-FR", { year: "numeric", month: "short", day: "2-digit", hour: "2-digit", minute: "2-digit" });
  } catch {
    return "";
  }
}

function formatBytes(bytes) {
  const n = Number(bytes);
  if (!Number.isFinite(n) || n <= 0) return "0 KB";
  if (n < 1024) return `${n} B`;
  if (n < 1024 * 1024) return `${(n / 1024).toFixed(1)} KB`;
  return `${(n / (1024 * 1024)).toFixed(2)} MB`;
}

function getSectionKey(section) {
  return String(section?.dataset?.section || "").trim();
}

function getSectionTitle(section) {
  const label = section?.dataset?.sectionTitle;
  if (label) return label;
  const title = section?.querySelector?.(".card__title");
  return title ? title.textContent.trim() : getSectionKey(section);
}

function isSectionLocked(section) {
  return section?.dataset?.sectionLocked === "true";
}

function normalizeSectionOrder(storedOrder, keysInDom) {
  const order = [];
  const seen = new Set();
  if (Array.isArray(storedOrder)) {
    for (const key of storedOrder) {
      if (!key || seen.has(key) || !sectionElements.has(key)) continue;
      seen.add(key);
      order.push(key);
    }
  }
  for (const key of keysInDom) {
    if (!key || seen.has(key)) continue;
    seen.add(key);
    order.push(key);
  }
  return order;
}

function applySectionOrder(order) {
  if (!sectionMain) sectionMain = document.querySelector(".app__main");
  if (!sectionMain) return;
  const settingsCard = document.querySelector(SECTION_SETTINGS_SELECTOR);
  for (const key of order) {
    const section = sectionElements.get(key);
    if (!section) continue;
    if (settingsCard) sectionMain.insertBefore(section, settingsCard);
    else sectionMain.appendChild(section);
  }
}

function applySectionVisibility(hidden) {
  sectionElements.forEach((section, key) => {
    const locked = isSectionLocked(section);
    const shouldHide = hidden.has(key) && !locked;
    section.classList.toggle(SECTION_HIDDEN_CLASS, shouldHide);
  });
}

function pruneHiddenSections() {
  let changed = false;
  for (const key of Array.from(hiddenSections)) {
    const section = sectionElements.get(key);
    if (!section || isSectionLocked(section)) {
      hiddenSections.delete(key);
      changed = true;
    }
  }
  if (changed) void setHiddenSections(Array.from(hiddenSections));
}

function updateSectionOrder(nextOrder, options = {}) {
  const { persist = true } = options;
  sectionOrder = nextOrder.slice();
  applySectionOrder(sectionOrder);
  renderSectionManager();
  if (persist) void setSectionOrder(sectionOrder);
}

function resetSectionOrder() {
  if (!defaultSectionOrder.length) return;
  updateSectionOrder(defaultSectionOrder);
}

function updateHiddenSections(nextHidden, options = {}) {
  const { persist = true } = options;
  hiddenSections = new Set(nextHidden);
  pruneHiddenSections();
  applySectionVisibility(hiddenSections);
  renderSectionManager();
  if (persist) void setHiddenSections(Array.from(hiddenSections));
  updateOnboardingVisibility();
}

function moveSection(key, delta) {
  const idx = sectionOrder.indexOf(key);
  if (idx < 0) return;
  const nextIdx = idx + delta;
  if (nextIdx < 0 || nextIdx >= sectionOrder.length) return;
  const next = sectionOrder.slice();
  next.splice(idx, 1);
  next.splice(nextIdx, 0, key);
  updateSectionOrder(next);
}

function reorderSectionList(order, draggedKey, targetKey, before) {
  const next = order.filter((key) => key !== draggedKey);
  const targetIndex = next.indexOf(targetKey);
  if (targetIndex < 0) {
    next.push(draggedKey);
    return next;
  }
  const insertIndex = before ? targetIndex : targetIndex + 1;
  next.splice(insertIndex, 0, draggedKey);
  return next;
}

function moveSectionToEnd(order, key) {
  const next = order.filter((item) => item !== key);
  next.push(key);
  return next;
}

function clearDropIndicators(list) {
  list.querySelectorAll(".section-row").forEach((row) => {
    row.classList.remove("is-drop-before", "is-drop-after");
  });
}

function clearDragState(list) {
  list.querySelectorAll(".section-row").forEach((row) => {
    row.classList.remove("is-drop-before", "is-drop-after", "is-dragging");
  });
}

function renderSectionManager() {
  const list = el("sectionManagerList");
  if (!list) return;
  list.innerHTML = "";

  const order = sectionOrder.length ? sectionOrder : Array.from(sectionElements.keys());
  order.forEach((key, index) => {
    const section = sectionElements.get(key);
    if (!section) return;

    const label = getSectionTitle(section);
    const locked = isSectionLocked(section);
    const visible = !hiddenSections.has(key) || locked;

    const row = document.createElement("div");
    row.className = "section-row";
    row.dataset.sectionKey = key;
    row.setAttribute("role", "listitem");

    const handle = document.createElement("button");
    handle.type = "button";
    handle.className = "section-row__handle";
    handle.draggable = true;
    handle.setAttribute("title", "Glisser pour dplacer");
    handle.setAttribute("aria-label", `Dplacer ${label}`);

    const labelEl = document.createElement("div");
    labelEl.className = "section-row__label";
    labelEl.textContent = label;

    const controls = document.createElement("div");
    controls.className = "section-row__controls";

    const upBtn = document.createElement("button");
    upBtn.type = "button";
    upBtn.className = "move-btn move-btn--up";
    upBtn.disabled = index === 0;
    upBtn.setAttribute("title", "Monter");
    upBtn.setAttribute("aria-label", `Monter ${label}`);
    upBtn.addEventListener("click", () => moveSection(key, -1));

    const downBtn = document.createElement("button");
    downBtn.type = "button";
    downBtn.className = "move-btn move-btn--down";
    downBtn.disabled = index === order.length - 1;
    downBtn.setAttribute("title", "Descendre");
    downBtn.setAttribute("aria-label", `Descendre ${label}`);
    downBtn.addEventListener("click", () => moveSection(key, 1));

    const toggleWrap = document.createElement("label");
    toggleWrap.className = "visibility-toggle";
    toggleWrap.setAttribute("title", locked ? "Toujours visible" : visible ? "Masquer" : "Afficher");
    const toggleInput = document.createElement("input");
    toggleInput.type = "checkbox";
    toggleInput.checked = visible;
    toggleInput.disabled = locked;
    toggleInput.setAttribute("aria-label", locked ? `${label} toujours visible` : `Afficher ${label}`);
    const toggleSlider = document.createElement("span");
    toggleSlider.className = "visibility-toggle__slider";
    toggleWrap.appendChild(toggleInput);
    toggleWrap.appendChild(toggleSlider);
    toggleInput.addEventListener("change", () => {
      if (locked) return;
      if (toggleInput.checked) hiddenSections.delete(key);
      else hiddenSections.add(key);
      applySectionVisibility(hiddenSections);
      void setHiddenSections(Array.from(hiddenSections));
      renderSectionManager();
    });

    controls.appendChild(upBtn);
    controls.appendChild(downBtn);
    if (locked) {
      const lockTag = document.createElement("span");
      lockTag.className = "section-row__lock";
      lockTag.textContent = "Fixe";
      lockTag.setAttribute("title", "Toujours visible");
      controls.appendChild(lockTag);
    }
    controls.appendChild(toggleWrap);

    row.appendChild(handle);
    row.appendChild(labelEl);
    row.appendChild(controls);
    list.appendChild(row);
  });
}

function initSectionDrag() {
  const list = el("sectionManagerList");
  if (!list) return;

  list.addEventListener("dragstart", (event) => {
    const handle = event.target.closest(".section-row__handle");
    if (!handle) return;
    const row = handle.closest(".section-row");
    if (!row) return;
    draggingSectionKey = row.dataset.sectionKey || "";
    row.classList.add("is-dragging");
    if (event.dataTransfer) {
      event.dataTransfer.effectAllowed = "move";
      event.dataTransfer.setData("text/plain", draggingSectionKey);
    }
  });

  list.addEventListener("dragover", (event) => {
    if (!draggingSectionKey) return;
    event.preventDefault();
    const row = event.target.closest(".section-row");
    if (!row || row.dataset.sectionKey === draggingSectionKey) return;
    clearDropIndicators(list);
    const rect = row.getBoundingClientRect();
    const before = event.clientY < rect.top + rect.height / 2;
    row.classList.toggle("is-drop-before", before);
    row.classList.toggle("is-drop-after", !before);
  });

  list.addEventListener("dragleave", (event) => {
    const row = event.target.closest(".section-row");
    if (!row) return;
    row.classList.remove("is-drop-before", "is-drop-after");
  });

  list.addEventListener("drop", (event) => {
    if (!draggingSectionKey) return;
    event.preventDefault();
    const row = event.target.closest(".section-row");
    const targetKey = row ? row.dataset.sectionKey || "" : "";
    const key = draggingSectionKey;
    draggingSectionKey = "";
    clearDragState(list);

    let nextOrder = sectionOrder.slice();
    if (!targetKey) {
      nextOrder = moveSectionToEnd(nextOrder, key);
    } else {
      const rect = row.getBoundingClientRect();
      const before = event.clientY < rect.top + rect.height / 2;
      nextOrder = reorderSectionList(nextOrder, key, targetKey, before);
    }
    updateSectionOrder(nextOrder);
  });

  list.addEventListener("dragend", () => {
    draggingSectionKey = "";
    clearDragState(list);
  });
}

async function initSectionManager() {
  sectionMain = document.querySelector(".app__main");
  if (!sectionMain) return;
  const sections = Array.from(sectionMain.querySelectorAll(SECTION_SELECTOR));
  sectionElements = new Map();
  sections.forEach((section) => {
    const key = getSectionKey(section);
    if (key) sectionElements.set(key, section);
  });

  const storedOrder = await getSectionOrder();
  const domKeys = sections.map((section) => getSectionKey(section)).filter(Boolean);
  defaultSectionOrder = domKeys.slice();
  sectionOrder = normalizeSectionOrder(storedOrder, domKeys);
  applySectionOrder(sectionOrder);

  hiddenSections = new Set(await getHiddenSections());
  pruneHiddenSections();
  applySectionVisibility(hiddenSections);

  renderSectionManager();
  initSectionDrag();
}

