function updateMaxTokensValue() {
  const exp = clampExp(el("maxTokensRange").value);
  const tokens = expToTokens(exp);
  el("maxTokensValue").textContent = formatNumber(tokens);
  return tokens;
}

function clampConcurrencyIndex(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return 0;
  return Math.max(0, Math.min(CONCURRENCY_LEVELS.length - 1, Math.round(n)));
}

function concurrencyIndexToValue(index) {
  return CONCURRENCY_LEVELS[clampConcurrencyIndex(index)];
}

function concurrencyValueToIndex(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return 0;
  let bestIndex = 0;
  let bestDiff = Math.abs(CONCURRENCY_LEVELS[0] - n);
  for (let i = 1; i < CONCURRENCY_LEVELS.length; i += 1) {
    const diff = Math.abs(CONCURRENCY_LEVELS[i] - n);
    if (diff < bestDiff) {
      bestDiff = diff;
      bestIndex = i;
    }
  }
  return bestIndex;
}

function updateConcurrencyValue() {
  const index = clampConcurrencyIndex(el("concurrencyRange").value);
  const value = concurrencyIndexToValue(index);
  el("concurrencyValue").textContent = formatNumber(value);
  return value;
}

function normalizeRpmLimitInput(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return DEFAULTS.rpmLimit;
  return Math.max(RPM_MIN, Math.min(RPM_MAX, Math.round(n)));
}

function updateRpmLimitValue() {
  const input = el("rpmLimitInput");
  if (!input) return DEFAULTS.rpmLimit;
  const value = normalizeRpmLimitInput(input.value);
  input.value = String(value);
  return value;
}

