// src/shared/providers.js
// Provider implementations (Gemini + OpenAI) and shared request orchestration.
//
// Requirements implemented:
// - Gemini 3 Flash (via Google Generative Language API)
// - OpenAI GPT-5 Mini (via OpenAI Responses API)
// - No temperature / sampling parameters are ever sent
// - Provider selection happens via taskpane default provider (formulas cannot override)
// - AI.WEB uses provider-native web search mechanisms:
//   - Gemini: google_search tool
//   - OpenAI: web_search tool

import {
  PROVIDERS,
  DEFAULTS,
  normalizeProvider,
  getDefaultProvider,
  getApiKey,
  getModel,
  getGeminiReasoningEffort,
  getOpenAIReasoningEffort,
  getMaxOutputTokens,
  getConcurrencyLimit,
  getRpmLimit,
  clampInt,
  nowMs,
  sleep,
  LRUCache,
  hashKey,
  logRequest,
  setRuntimeStatus,
  getCacheClearAt,
  getPersistentCache,
  setPersistentCache,
  clearPersistentCache,
  getPrivacyMode,
  redactSensitiveText
} from "./core.js";

// ---- In-memory cache (per runtime) ----
const CACHE = new LRUCache(800);
let _cacheClearLocalMs = 0;

async function syncCacheClearMarker() {
  const clearedAt = await getCacheClearAt();
  if (clearedAt > _cacheClearLocalMs) {
    _cacheClearLocalMs = clearedAt;
    CACHE.clear();
  }
}

// Deduplicate in-flight identical requests to avoid bursts (per runtime)
const INFLIGHT = new Map(); // key => Promise<AIResult>

const GEMINI_503_RETRY_DELAY_MS = 10000;
const GEMINI_503_MAX_RETRIES = 2;
const GEMINI_CACHED_CONTENT_TTL_MS = 15 * 60 * 1000;
const GEMINI_CACHED_CONTENT_MIN_PREFIX_CHARS = 200;
const GEMINI_CACHED_CONTENT_MIN_SUFFIX_CHARS = 1;
const GEMINI_CACHED_CONTENT = new Map(); // key => {name, createdAtMs, expiresAtMs, chargedTokenHours}
const GEMINI_CACHED_INFLIGHT = new Map(); // key => Promise<{name, cacheKey} | null>

// Concurrency limiter (per runtime)
const QUEUE_CANCELLED = Symbol("queue_cancelled");
const QUEUE_CANCELLED_RESULT = { ok: false, code: "QUEUE_CANCELLED", message: "File d'attente arrte." };
const CONCURRENCY_CACHE_TTL_MS = 1500;
let concurrencyLimitCached = DEFAULTS.concurrencyLimit;
let concurrencyLimitCachedAt = 0;

// RPM limiter (per runtime, global across providers)
const RPM_WINDOW_MS = 60000;
const RPM_CACHE_TTL_MS = 1500;
let rpmLimitCached = DEFAULTS.rpmLimit;
let rpmLimitCachedAt = 0;
let rpmWindow = [];
let rpmGate = Promise.resolve();
let rpmStatusTimer = null;

class FastQueue {
  constructor() {
    this._items = [];
    this._head = 0;
  }

  get length() {
    return this._items.length - this._head;
  }

  push(value) {
    this._items.push(value);
  }

  shift() {
    if (this._head >= this._items.length) return undefined;
    const value = this._items[this._head++];
    if (this._head > 1024 && this._head * 2 > this._items.length) {
      this._items = this._items.slice(this._head);
      this._head = 0;
    }
    return value;
  }

  drain() {
    const pending = this._items.slice(this._head);
    this._items = [];
    this._head = 0;
    return pending;
  }
}

let active = 0;
const queue = new FastQueue();
let statusTimer = null;
let pendingStatus = null;
let lastStatusKey = "";
const BATCH_REQUEST_THRESHOLD = Number.POSITIVE_INFINITY;
const BATCH_LOG_THROTTLE_MS = 1000;
let batchState = null;

function createBatchState() {
  const startedAt = nowMs();
  return {
    id: `batch_${startedAt}_${Math.random().toString(36).slice(2, 8)}`,
    startedAt,
    endedAt: 0,
    totalRequests: 0,
    okCount: 0,
    errorCount: 0,
    cachedCount: 0,
    inputTokens: 0,
    outputTokens: 0,
    reasoningTokens: 0,
    cachedInputTokens: 0,
    msSum: 0,
    msMax: 0,
    msMin: 0,
    peakQueued: 0,
    peakActive: 0,
    models: new Map(),
    lastLogAt: 0
  };
}

function totalPendingRequests() {
  return active + queue.length;
}

function ensureBatchState() {
  if (batchState) return true;
  if (totalPendingRequests() < BATCH_REQUEST_THRESHOLD) return false;
  batchState = createBatchState();
  return true;
}

function updateBatchPeaks() {
  if (!batchState) return;
  batchState.peakQueued = Math.max(batchState.peakQueued, queue.length);
  batchState.peakActive = Math.max(batchState.peakActive, active);
}

function recordBatchResult({ provider, model, ok, cached, usage, ms, cacheAdjusted = false }) {
  if (!batchState) return false;
  batchState.totalRequests += 1;
  if (ok) batchState.okCount += 1;
  else batchState.errorCount += 1;
  if (cached) batchState.cachedCount += 1;

  const inputTokens = Number(usage?.inputTokens || 0) || 0;
  const outputTokens = Number(usage?.outputTokens || 0) || 0;
  const reasoningTokens = Number(usage?.reasoningTokens || 0) || 0;
  const cachedInputTokens = Number(usage?.cachedInputTokens || 0) || 0;

  batchState.inputTokens += inputTokens;
  batchState.outputTokens += outputTokens;
  batchState.reasoningTokens += reasoningTokens;
  batchState.cachedInputTokens += cachedInputTokens;

  const duration = Number(ms || 0) || 0;
  if (duration > 0) {
    batchState.msSum += duration;
    batchState.msMax = Math.max(batchState.msMax, duration);
    batchState.msMin = batchState.msMin ? Math.min(batchState.msMin, duration) : duration;
  }

  if (inputTokens + outputTokens + reasoningTokens + cachedInputTokens > 0) {
    const key = `${String(provider || "").toLowerCase()}|${String(model || "").trim()}`;
    const existing = batchState.models.get(key) || {
      provider: String(provider || "").toLowerCase(),
      model: String(model || "").trim(),
      inputTokens: 0,
      outputTokens: 0,
      reasoningTokens: 0,
      cachedInputTokens: 0,
      requests: 0,
      cacheAdjusted: false
    };
    existing.inputTokens += inputTokens;
    existing.outputTokens += outputTokens;
    existing.reasoningTokens += reasoningTokens;
    existing.cachedInputTokens += cachedInputTokens;
    existing.requests += 1;
    if (cacheAdjusted) existing.cacheAdjusted = true;
    batchState.models.set(key, existing);
  }

  maybeEmitBatchLog(false);
  return true;
}

function shouldUsePersistentCache() {
  if (batchState) return false;
  if (totalPendingRequests() >= BATCH_REQUEST_THRESHOLD) return false;
  return true;
}

function buildBatchLogEntry(isFinal) {
  if (!batchState) return null;
  const models = Array.from(batchState.models.values());
  const totalTokens = batchState.inputTokens + batchState.outputTokens + batchState.reasoningTokens;
  const durationMs = Math.max(0, (batchState.endedAt || nowMs()) - batchState.startedAt);
  const avgMs = batchState.totalRequests > 0 ? Math.round(batchState.msSum / batchState.totalRequests) : 0;

  return {
    fn: "AI.BATCH",
    provider: "batch",
    model: "",
    ok: batchState.errorCount === 0,
    cache: false,
    ms: durationMs,
    inputTokens: batchState.inputTokens,
    outputTokens: batchState.outputTokens,
    reasoningTokens: batchState.reasoningTokens,
    cachedInputTokens: batchState.cachedInputTokens,
    totalTokens,
    batch: true,
    batchId: batchState.id,
    batchLive: !isFinal,
    batchModels: models,
    batchStats: {
      totalRequests: batchState.totalRequests,
      ok: batchState.okCount,
      error: batchState.errorCount,
      cached: batchState.cachedCount,
      avgMs,
      maxMs: batchState.msMax,
      minMs: batchState.msMin,
      peakQueued: batchState.peakQueued,
      peakActive: batchState.peakActive
    }
  };
}

function maybeEmitBatchLog(force) {
  if (!batchState) return;
  const now = nowMs();
  if (!force && now - batchState.lastLogAt < BATCH_LOG_THROTTLE_MS) return;
  batchState.lastLogAt = now;
  const entry = buildBatchLogEntry(false);
  if (entry) logRequest(entry);
}

function finalizeBatchIfIdle() {
  if (!batchState) return;
  if (active > 0 || queue.length > 0) return;
  batchState.endedAt = nowMs();
  const entry = buildBatchLogEntry(true);
  batchState = null;
  if (entry) logRequest(entry);
}

function getRpmCount() {
  const now = nowMs();
  pruneRpmWindow(now);
  return rpmWindow.length;
}

function snapshotQueueStatus() {
  return { active, queued: queue.length, rpm: getRpmCount() };
}

function scheduleRuntimeStatusUpdate() {
  const snapshot = snapshotQueueStatus();
  pendingStatus = snapshot;
  if (statusTimer) return;
  statusTimer = setTimeout(() => {
    statusTimer = null;
    const status = pendingStatus;
    pendingStatus = null;
    if (!status) return;
    const key = `${status.active}:${status.queued}:${status.rpm}`;
    if (key === lastStatusKey) return;
    lastStatusKey = key;
    void setRuntimeStatus({ ...status, ts: nowMs() });
  }, 120);
}

async function getConcurrencyLimitCached() {
  const now = nowMs();
  if (now - concurrencyLimitCachedAt < CONCURRENCY_CACHE_TTL_MS) return concurrencyLimitCached;
  const value = await getConcurrencyLimit();
  concurrencyLimitCached = value;
  concurrencyLimitCachedAt = now;
  return value;
}

async function getRpmLimitCached() {
  const now = nowMs();
  if (now - rpmLimitCachedAt < RPM_CACHE_TTL_MS) return rpmLimitCached;
  const value = await getRpmLimit();
  rpmLimitCached = value;
  rpmLimitCachedAt = now;
  return value;
}

function pruneRpmWindow(now) {
  const cutoff = now - RPM_WINDOW_MS;
  while (rpmWindow.length && rpmWindow[0] <= cutoff) {
    rpmWindow.shift();
  }
}

function ensureRpmStatusTimer() {
  if (rpmStatusTimer) return;
  rpmStatusTimer = setInterval(() => {
    const count = getRpmCount();
    scheduleRuntimeStatusUpdate();
    if (count <= 0) {
      clearInterval(rpmStatusTimer);
      rpmStatusTimer = null;
    }
  }, 1000);
}

async function reserveRpmSlot(limit) {
  while (true) {
    const now = nowMs();
    pruneRpmWindow(now);
    if (!limit || limit <= 0 || rpmWindow.length < limit) {
      rpmWindow.push(now);
      ensureRpmStatusTimer();
      scheduleRuntimeStatusUpdate();
      return;
    }
    const waitMs = Math.max(0, rpmWindow[0] + RPM_WINDOW_MS - now);
    if (waitMs > 0) {
      await sleep(waitMs);
    } else {
      await sleep(0);
    }
  }
}

async function waitForRpmSlot() {
  const limit = await getRpmLimitCached();
  if (!limit || limit <= 0) return;
  rpmGate = rpmGate.then(() => reserveRpmSlot(limit));
  return rpmGate;
}

export function stopQueuedRequests() {
  if (!queue.length) return 0;
  const pending = queue.drain();
  for (const resolve of pending) {
    resolve(QUEUE_CANCELLED);
  }
  scheduleRuntimeStatusUpdate();
  return pending.length;
}

async function withConcurrencyLimit(fn) {
  while (true) {
    const limit = await getConcurrencyLimitCached();
    if (active < limit) break;
    const gate = await new Promise((resolve) => {
      queue.push(resolve);
      ensureBatchState();
      updateBatchPeaks();
      scheduleRuntimeStatusUpdate();
    });
    if (gate === QUEUE_CANCELLED) return { ...QUEUE_CANCELLED_RESULT };
  }
  active++;
  ensureBatchState();
  updateBatchPeaks();
  scheduleRuntimeStatusUpdate();
  try {
    await waitForRpmSlot();
    return await fn();
  } finally {
    active--;
    const next = queue.shift();
    if (next) next();
    scheduleRuntimeStatusUpdate();
    finalizeBatchIfIdle();
  }
}

// Reset runtime status on load to avoid stale UI.
void setRuntimeStatus({ active: 0, queued: 0, rpm: 0, ts: nowMs() });

function isNonEmptyString(s) {
  return typeof s === "string" && s.trim().length > 0;
}

function normalizeBoolean(v, def = false) {
  if (typeof v === "boolean") return v;
  if (typeof v === "number") return v !== 0;
  if (typeof v === "string") {
    const s = v.trim().toLowerCase();
    if (["1", "true", "yes", "y", "on"].includes(s)) return true;
    if (["0", "false", "no", "n", "off"].includes(s)) return false;
  }
  return def;
}

function normalizeTimeoutMs(v) {
  return clampInt(v, 1000, 120000, DEFAULTS.timeoutMs);
}

function normalizeRetry(v) {
  return clampInt(v, 0, 5, DEFAULTS.retry);
}

function normalizeMaxOutputTokens(v) {
  return clampInt(v, 1, 128000, DEFAULTS.maxOutputTokens);
}

function toLowerMsg(v) {
  return String(v || "").toLowerCase();
}

function isTimeoutError(err) {
  if (!err) return false;
  if (err.name === "AbortError") return true;
  const msg = String(err.message || "").toLowerCase();
  return msg.includes("aborted") || msg.includes("timeout");
}

function toGeminiModelRef(model) {
  const id = String(model || "").trim();
  if (!id) return "";
  return id.startsWith("models/") ? id : `models/${id}`;
}

function pruneGeminiCachedContent() {
  const now = nowMs();
  for (const [key, entry] of GEMINI_CACHED_CONTENT.entries()) {
    if (entry?.expiresAtMs && entry.expiresAtMs <= now) {
      GEMINI_CACHED_CONTENT.delete(key);
    }
  }
}

async function createGeminiCachedContent({ apiKey, model, system, prefix, ttlMs, timeoutMs }) {
  const url = "https://generativelanguage.googleapis.com/v1beta/cachedContents";
  const body = {
    model: toGeminiModelRef(model),
    contents: [{ role: "user", parts: [{ text: prefix }] }],
    ttl: `${Math.max(60, Math.floor((ttlMs || GEMINI_CACHED_CONTENT_TTL_MS) / 1000))}s`
  };

  if (isNonEmptyString(system)) {
    body.systemInstruction = { role: "system", parts: [{ text: system }] };
  }

  const res = await fetchWithTimeout(
    url,
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-goog-api-key": apiKey
      },
      body: JSON.stringify(body)
    },
    Math.max(10000, Math.min(60000, timeoutMs || 30000))
  );

  const json = await safeJson(res);
  if (!res.ok) return { ok: false, status: res.status, json };
  const name = String(json?.name || "").trim();
  if (!name) return { ok: false, status: res.status, json };
  const expireTime = json?.expireTime ? Date.parse(String(json.expireTime)) : 0;
  return { ok: true, name, expireTime };
}

async function ensureGeminiCachedContent({ apiKey, model, system, prefix, ttlMs, timeoutMs }) {
  if (!apiKey || !isNonEmptyString(prefix)) return null;
  if (await getPrivacyMode()) return null;
  if (prefix.length < GEMINI_CACHED_CONTENT_MIN_PREFIX_CHARS) return null;

  pruneGeminiCachedContent();
  const cacheKey = await hashKey({ v: 1, model: String(model || "").trim(), system: String(system || "").trim(), prefix });
  const existing = GEMINI_CACHED_CONTENT.get(cacheKey);
  if (existing?.name) {
    if (!Number.isFinite(existing.createdAtMs) || existing.createdAtMs <= 0) {
      existing.createdAtMs = nowMs();
    }
    if (!Number.isFinite(existing.expiresAtMs) || existing.expiresAtMs <= existing.createdAtMs) {
      existing.expiresAtMs = existing.createdAtMs + (ttlMs || GEMINI_CACHED_CONTENT_TTL_MS);
    }
    if (!Number.isFinite(existing.chargedTokenHours)) existing.chargedTokenHours = 0;
    GEMINI_CACHED_CONTENT.set(cacheKey, existing);
    return { name: existing.name, cacheKey };
  }

  if (GEMINI_CACHED_INFLIGHT.has(cacheKey)) return GEMINI_CACHED_INFLIGHT.get(cacheKey);

  const p = (async () => {
    try {
      const created = await createGeminiCachedContent({ apiKey, model, system, prefix, ttlMs, timeoutMs });
      if (!created?.ok || !created?.name) return null;
      const expiresAtMs = Number.isFinite(created.expireTime) && created.expireTime > 0
        ? created.expireTime
        : nowMs() + (ttlMs || GEMINI_CACHED_CONTENT_TTL_MS);
      GEMINI_CACHED_CONTENT.set(cacheKey, {
        name: created.name,
        createdAtMs: nowMs(),
        expiresAtMs,
        chargedTokenHours: 0
      });
      return { name: created.name, cacheKey };
    } catch {
      return null;
    }
  })();

  GEMINI_CACHED_INFLIGHT.set(cacheKey, p);
  try {
    return await p;
  } finally {
    GEMINI_CACHED_INFLIGHT.delete(cacheKey);
  }
}

function looksLikeCachedContentInvalid(message) {
  const s = toLowerMsg(message);
  if (!s || !s.includes("cached")) return false;
  return (
    s.includes("not found") ||
    s.includes("invalid") ||
    s.includes("expired") ||
    s.includes("does not exist")
  );
}

function looksLikeStructuredOutputUnsupported(message) {
  const s = toLowerMsg(message);
  if (!s) return false;
  return (
    s.includes("responseschema") ||
    s.includes("response schema") ||
    s.includes("response_schema") ||
    s.includes("responsemime") ||
    s.includes("response mime") ||
    s.includes("response_mime") ||
    s.includes("response_mime_type") ||
    s.includes("json_schema") ||
    s.includes("response_format") ||
    s.includes("text.format")
  );
}

function looksLikeWebSearchUnsupported(message) {
  const s = toLowerMsg(message);
  if (!s) return false;
  if (!(s.includes("web_search") || s.includes("google_search") || s.includes("web search") || s.includes("google search"))) {
    return false;
  }
  return (
    s.includes("not supported") ||
    s.includes("unsupported") ||
    s.includes("unknown") ||
    s.includes("unrecognized") ||
    s.includes("invalid") ||
    s.includes("not enabled") ||
    s.includes("not available")
  );
}

function isGeminiServiceUnavailable(provider, result) {
  if (provider !== PROVIDERS.GEMINI || result?.ok) return false;
  if (result?.status === 503) return true;
  if (result?.code !== "API_ERROR") return false;
  const msg = toLowerMsg(result?.message);
  if (!msg) return false;
  return msg.includes("overloaded") || msg.includes("service unavailable") || msg.includes("unavailable");
}

function ensureJsonInstruction(system) {
  const suffix = " Return valid JSON only.";
  if (!isNonEmptyString(system)) return "Return valid JSON only.";
  const s = String(system).trim();
  if (/json/i.test(s)) return s;
  return s + suffix;
}

function extractFormulaFromText(text) {
  if (!isNonEmptyString(text)) return "";
  const raw = String(text).trim();
  const match = raw.match(/=[^\n\r]+/);
  const formula = (match ? match[0] : raw).trim();
  if (!formula.startsWith("=")) return "";
  return formula;
}

function getFormulaForLog(fnName, text) {
  if (fnName !== "AI.FORMULA") return "";
  return extractFormulaFromText(text);
}

function resolveGeminiThinkingConfig(model, effort) {
  const level = String(effort || "").trim().toLowerCase();
  if (!level || level === "auto" || level === "none") return null;

  const modelId = String(model || "").trim().toLowerCase();
  if (!modelId.includes("gemini")) return null;

  if (modelId.includes("gemini-3")) {
    if (modelId.includes("flash")) {
      const allowed = new Set(["minimal", "low", "medium", "high"]);
      const normalized = allowed.has(level) ? level : "low";
      return { thinkingLevel: normalized.toUpperCase() };
    }
    const normalized = level === "high" || level === "medium" ? "high" : "low";
    return { thinkingLevel: normalized.toUpperCase() };
  }

  if (modelId.includes("gemini-2.5")) {
    const budgets = {
      minimal: 512,
      low: 2048,
      medium: 8192,
      high: 24576
    };
    let budget = budgets[level];
    if (!budget) return null;
    if (modelId.includes("flash-lite")) budget = Math.max(512, budget);
    if (modelId.includes("pro")) budget = Math.max(128, budget);
    return { thinkingBudget: budget };
  }

  return null;
}

// ---- Public API ----

/**
 * @typedef {Object} AIGenerateRequest
 * @property {string=} provider Optional override (should only be used by taskpane tests, not formulas).
 * @property {string=} system System instruction.
 * @property {string} user User prompt.
 * @property {boolean=} webSearch Enable provider web search tool.
 * @property {number=} maxOutputTokens Override max tokens (optional; normally from panel).
 * @property {number=} timeoutMs
 * @property {number=} retry
 * @property {boolean=} cache
 * @property {number=} cacheTtlSec
 * @property {string=} cachePrefix Explicit cache prefix (Gemini only, used by AI.BATCH).
 * @property {string=} cacheSuffix Explicit cache suffix (Gemini only, used by AI.BATCH).
 * @property {string=} responseMimeType e.g., "application/json"
 * @property {Object=} responseJsonSchema JSON schema (strict) for structured output.
 * @property {string=} functionName Name for diagnostics.
 * @property {string=} functionCall Excel-style function call for diagnostics.
 */

/**
 * @typedef {Object} AIResult
 * @property {boolean} ok
 * @property {string=} text
 * @property {Array<{title?: string, url: string}>=} sources
 * @property {string=} provider
 * @property {string=} model
 * @property {Object=} usage
 * @property {number=} status
 * @property {string=} code
 * @property {string=} message
 * @property {string=} cacheHint
 * @property {number=} cacheStorageTokenHours
 */

export async function aiGenerate(req) {
  const start = nowMs();
  const provider = normalizeProvider(req?.provider) || (await getDefaultProvider());
  const model = await getModel(provider);
  const apiKey = await getApiKey(provider);
  const fnName = req?.functionName || "aiGenerate";
  const functionCall = isNonEmptyString(req?.functionCall) ? String(req.functionCall) : "";
  const reasoningEffort = provider === PROVIDERS.OPENAI ? await getOpenAIReasoningEffort() : "";
  const geminiReasoningEffort = provider === PROVIDERS.GEMINI ? await getGeminiReasoningEffort() : "";
  const reasoningEffortLabel = provider === PROVIDERS.OPENAI ? reasoningEffort : geminiReasoningEffort;

  if (!apiKey) {
    logRequest({
      fn: fnName,
      provider,
      model,
      ok: false,
      ms: nowMs() - start,
      cache: false,
      reasoningEffort: reasoningEffortLabel || undefined,
      call: functionCall || undefined,
      code: "API_KEY_MISSING",
      message: `Cl API manquante pour le fournisseur ${provider}.`
    });
    return {
      ok: false,
      provider,
      model,
      code: "API_KEY_MISSING",
      message: `Cl API manquante pour le fournisseur ${provider}.`
    };
  }

  const cachePrefix = isNonEmptyString(req?.cachePrefix) ? String(req.cachePrefix) : "";
  const cacheSuffix = isNonEmptyString(req?.cacheSuffix) ? String(req.cacheSuffix) : "";
  const user = cachePrefix ? cachePrefix + cacheSuffix : String(req?.user || "");
  const system = isNonEmptyString(req?.system) ? String(req.system) : "";

  const webSearch = normalizeBoolean(req?.webSearch, false);
  const timeoutMs = normalizeTimeoutMs(req?.timeoutMs);
  const retry = normalizeRetry(req?.retry);
  const gemini503RetryMax = Math.min(retry, GEMINI_503_MAX_RETRIES);

  const cfgMax = await getMaxOutputTokens();
  const maxOutputTokens = normalizeMaxOutputTokens(req?.maxOutputTokens ?? cfgMax);
  const thinkingConfig = provider === PROVIDERS.GEMINI ? resolveGeminiThinkingConfig(model, geminiReasoningEffort) : null;

  const cacheEnabled = normalizeBoolean(req?.cache, DEFAULTS.cache);
  const ttlSec = clampInt(
    req?.cacheTtlSec ?? DEFAULTS.cacheTtlSec,
    1,
    365 * 24 * 3600,
    DEFAULTS.cacheTtlSec
  );
  const allowPersistentCache = cacheEnabled && shouldUsePersistentCache();

  await syncCacheClearMarker();

  // Cache key (no temperature anywhere)
  const cacheKeyPayload = {
    v: 3,
    provider,
    model,
    maxOutputTokens,
    reasoningEffort,
    geminiReasoningEffort,
    webSearch,
    system,
    user,
    responseMimeType: req?.responseMimeType || "",
    responseJsonSchema: req?.responseJsonSchema || null
  };

  const cacheKey = await hashKey(cacheKeyPayload);

  if (cacheEnabled) {
    const cached = CACHE.get(cacheKey);
    if (cached) {
      const formula = getFormulaForLog(fnName, cached?.text);
      const recorded = recordBatchResult({
        provider,
        model,
        ok: true,
        cached: true,
        usage: null,
        ms: nowMs() - start
      });
      if (!recorded) {
        logRequest({
          fn: fnName,
          provider,
          model,
          ok: true,
          ms: nowMs() - start,
          cache: true,
          cacheKind: "memory",
          reasoningEffort: reasoningEffortLabel || undefined,
          call: functionCall || undefined,
          inputTokens: 0,
          outputTokens: 0,
          reasoningTokens: 0,
          totalTokens: 0,
          formula: formula || undefined
        });
      }
      if (recorded) finalizeBatchIfIdle();
      return { ...cached, cached: true, cacheKind: "memory" };
    }
    if (allowPersistentCache) {
      const persisted = await getPersistentCache(cacheKey);
      if (persisted) {
        const formula = getFormulaForLog(fnName, persisted?.text);
        CACHE.set(cacheKey, persisted, ttlSec * 1000);
        const recorded = recordBatchResult({
          provider,
          model,
          ok: true,
          cached: true,
          usage: null,
          ms: nowMs() - start
        });
        if (!recorded) {
          logRequest({
            fn: fnName,
            provider,
            model,
            ok: true,
            ms: nowMs() - start,
            cache: true,
            cacheKind: "persistent",
            reasoningEffort: reasoningEffortLabel || undefined,
            call: functionCall || undefined,
            inputTokens: 0,
            outputTokens: 0,
            reasoningTokens: 0,
            totalTokens: 0,
            formula: formula || undefined
          });
        }
        if (recorded) finalizeBatchIfIdle();
        return { ...persisted, cached: true, cacheKind: "persistent" };
      }
    }
  }

  if (INFLIGHT.has(cacheKey)) return INFLIGHT.get(cacheKey);

  const p = withConcurrencyLimit(async () => {
    let attempt = 0;
    let lastErr = null;

    const callProvider = async (opts) =>
      provider === PROVIDERS.OPENAI ? callOpenAI(opts) : callGemini(opts);

    while (attempt <= retry) {
      attempt++;
      try {
        const baseOpts = {
          apiKey,
          model,
          system,
          user,
          cachePrefix: cachePrefix || undefined,
          cacheSuffix: cacheSuffix || undefined,
          maxOutputTokens,
          reasoningEffort,
          thinkingConfig,
          timeoutMs,
          webSearch,
          responseMimeType: req?.responseMimeType,
          responseJsonSchema: req?.responseJsonSchema
        };

        let result = await callProvider(baseOpts);

        if (!result.ok && !isGeminiServiceUnavailable(provider, result)) {
          let opts = baseOpts;

          // Fallback #1: if web search fails, retry without tools.
          if (opts.webSearch) {
            const shouldRetryNoWeb =
              looksLikeWebSearchUnsupported(result.message) || result.code === "API_ERROR" || result.code === "EMPTY_RESPONSE";
            if (shouldRetryNoWeb) {
              opts = { ...opts, webSearch: false };
              result = await callProvider(opts);
            }
          }

          // Fallback #2: if structured output fails, retry without schema/responseMimeType.
          const wantsStructured = opts.responseMimeType || opts.responseJsonSchema;
          if (!result.ok && wantsStructured) {
            const shouldRetryPlainJson =
              looksLikeStructuredOutputUnsupported(result.message) ||
              result.code === "API_ERROR" ||
              result.code === "EMPTY_RESPONSE";
            if (shouldRetryPlainJson) {
              opts = {
                ...opts,
                responseMimeType: undefined,
                responseJsonSchema: undefined,
                system: ensureJsonInstruction(opts.system)
              };
              result = await callProvider(opts);
            }
          }
        }

        const shouldRetryServiceUnavailable =
          isGeminiServiceUnavailable(provider, result) && attempt <= gemini503RetryMax;

        // Log (best-effort)
        let usage = normalizeUsage(result?.usage);
        const cacheAdjusted = provider === PROVIDERS.GEMINI && usage.cachedInputTokens > 0;
        if (cacheAdjusted) {
          usage = {
            ...usage,
            inputTokens: Math.max(0, usage.inputTokens - usage.cachedInputTokens)
          };
        }
        const formula = result.ok ? getFormulaForLog(fnName, result.text) : "";

        if (shouldRetryServiceUnavailable) {
          await sleep(GEMINI_503_RETRY_DELAY_MS);
          continue;
        }

        const recorded = recordBatchResult({
          provider,
          model,
          ok: result.ok,
          cached: false,
          usage,
          ms: nowMs() - start,
          cacheAdjusted
        });
        if (!recorded) {
          logRequest({
            fn: fnName,
            provider,
            model,
            ok: result.ok,
            ms: nowMs() - start,
            attempt,
            cache: false,
            reasoningEffort: reasoningEffortLabel || undefined,
            call: functionCall || undefined,
            inputTokens: usage.inputTokens,
            outputTokens: usage.outputTokens,
            reasoningTokens: usage.reasoningTokens,
            cachedInputTokens: usage.cachedInputTokens,
            totalTokens: usage.totalTokens,
            code: result.code,
            message: result.message,
            formula: formula || undefined,
            cacheHint: result.cacheHint || undefined,
            cacheStorageTokenHours: result.cacheStorageTokenHours || 0,
            cacheAdjusted
          });
        }

        if (result.ok && cacheEnabled) {
          CACHE.set(cacheKey, result, ttlSec * 1000);
          if (allowPersistentCache) {
            await setPersistentCache(cacheKey, result, ttlSec * 1000, { provider, model });
          }
        }

        return result;
      } catch (e) {
        lastErr = e;
        if (attempt <= retry) await sleep(250 * attempt);
      }
    }

    const timeout = isTimeoutError(lastErr);
    const code = timeout ? "TIMEOUT" : "API_ERROR";
    const rawMsg = timeout ? "Dlai dpass (timeout)." : (lastErr?.message ? String(lastErr.message) : "Erreur inconnue.");
    const msg = redactSensitiveText(rawMsg);
    const recorded = recordBatchResult({
      provider,
      model,
      ok: false,
      cached: false,
      usage: null,
      ms: nowMs() - start
    });
    if (!recorded) {
      logRequest({
        fn: fnName,
        provider,
        model,
        ok: false,
        ms: nowMs() - start,
        cache: false,
        reasoningEffort: reasoningEffortLabel || undefined,
        call: functionCall || undefined,
        code,
        message: msg
      });
    }
    return { ok: false, provider, model, code, message: msg };
  });

  INFLIGHT.set(cacheKey, p);

  try {
    const result = await p;
    if (result?.code === "QUEUE_CANCELLED") {
      const recorded = recordBatchResult({
        provider,
        model,
        ok: false,
        cached: false,
        usage: null,
        ms: nowMs() - start
      });
      if (!recorded) {
        logRequest({
          fn: fnName,
          provider,
          model,
          ok: false,
          ms: nowMs() - start,
          cache: false,
          reasoningEffort: reasoningEffortLabel || undefined,
          call: functionCall || undefined,
          inputTokens: 0,
          outputTokens: 0,
          reasoningTokens: 0,
          cachedInputTokens: 0,
          totalTokens: 0,
          code: result.code,
          message: result.message
        });
      }
    }
    return result;
  } finally {
    INFLIGHT.delete(cacheKey);
  }
}

/**
 * Lightweight connectivity test for a provider (used from taskpane and AI.TEST()).
 */
export async function aiMinimalTest(providerOverride) {
  const provider = normalizeProvider(providerOverride) || (await getDefaultProvider());
  const model = await getModel(provider);
  const apiKey = await getApiKey(provider);

  if (!apiKey) {
    return {
      ok: false,
      provider,
      model,
      code: "API_KEY_MISSING",
      message: `Cl API manquante pour le fournisseur ${provider}.`
    };
  }

  // Tiny request
  return await aiGenerate({
    provider,
    system: "You are a test endpoint. Reply with exactly: OK",
    user: "OK",
    maxOutputTokens: 500,
    cache: false,
    webSearch: false,
    functionName: "AI.MINIMAL_TEST"
  });
}

// ---- Provider: Gemini (Google Generative Language API) ----

function geminiEndpoint(model) {
  return `https://generativelanguage.googleapis.com/v1beta/models/${encodeURIComponent(model)}:generateContent`;
}

async function callGemini({
  apiKey,
  model,
  system,
  user,
  cachePrefix,
  cacheSuffix,
  maxOutputTokens,
  thinkingConfig,
  timeoutMs,
  webSearch,
  responseMimeType,
  responseJsonSchema,
  disableCache = false
}) {
  const url = geminiEndpoint(model);
  let cacheHint = "";
  let cachedContentName = "";
  let cachedContentKey = "";
  let userText = user;

  const systemText = isNonEmptyString(system)
    ? ((responseMimeType === "application/json" || responseJsonSchema)
      ? system + " You must return valid JSON."
      : system)
    : "";

  const shouldUseExplicitCache = !disableCache
    && !webSearch
    && isNonEmptyString(cachePrefix)
    && isNonEmptyString(cacheSuffix)
    && cacheSuffix.trim().length >= GEMINI_CACHED_CONTENT_MIN_SUFFIX_CHARS;

  if (shouldUseExplicitCache) {
    const cached = await ensureGeminiCachedContent({
      apiKey,
      model,
      system: systemText,
      prefix: cachePrefix,
      ttlMs: GEMINI_CACHED_CONTENT_TTL_MS,
      timeoutMs
    });
    if (cached?.name) {
      cachedContentName = cached.name;
      cachedContentKey = cached.cacheKey || "";
      userText = cacheSuffix;
      cacheHint = "gemini_cached_content";
    }
  }

  const body = {
    contents: [{ role: "user", parts: [{ text: userText }] }],
    generationConfig: {
      maxOutputTokens
    }
  };

  if (thinkingConfig && typeof thinkingConfig === "object") {
    body.generationConfig.thinkingConfig = thinkingConfig;
  }

  if (isNonEmptyString(systemText) && !cachedContentName) {
    // API uses lowerCamelCase for this field in JSON representation
    // Force explicit JSON instruction for robustness
    body.systemInstruction = { role: "system", parts: [{ text: systemText }] };
  }

  // Structured output (JSON) when requested
  if (responseMimeType) {
    body.generationConfig.responseMimeType = responseMimeType;
  }
  if (responseJsonSchema) {
    body.generationConfig.responseSchema = responseJsonSchema;
  }

  // Web grounding tool
  if (webSearch) {
    // For REST v1beta, the tool name is usually google_search (snake_case).
    // Note: Some newer endpoints might respect googleSearch, but snake_case is safer for REST.
    body.tools = [{ google_search: {} }];
  }
  if (cachedContentName) {
    body.cachedContent = cachedContentName;
  }

  const res = await fetchWithTimeout(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "x-goog-api-key": apiKey
    },
    body: JSON.stringify(body)
  }, timeoutMs);

  const json = await safeJson(res);
  const errInfo = extractGeminiErrorInfo(json);

  if (errInfo?.message) {
    if (cachedContentKey && looksLikeCachedContentInvalid(errInfo.message)) {
      GEMINI_CACHED_CONTENT.delete(cachedContentKey);
      return await callGemini({
        apiKey,
        model,
        system,
        user,
        cachePrefix,
        cacheSuffix,
        maxOutputTokens,
        thinkingConfig,
        timeoutMs,
        webSearch,
        responseMimeType,
        responseJsonSchema,
        disableCache: true
      });
    }
    return {
      ok: false,
      provider: PROVIDERS.GEMINI,
      model,
      status: errInfo.status ?? res.status,
      code: "API_ERROR",
      message: redactSensitiveText(errInfo.message),
      cacheHint
    };
  }

  if (!res.ok) {
    const msg = extractGeminiError(json) || `Gemini API error (${res.status}).`;
    if (cachedContentKey && looksLikeCachedContentInvalid(msg)) {
      GEMINI_CACHED_CONTENT.delete(cachedContentKey);
      return await callGemini({
        apiKey,
        model,
        system,
        user,
        cachePrefix,
        cacheSuffix,
        maxOutputTokens,
        thinkingConfig,
        timeoutMs,
        webSearch,
        responseMimeType,
        responseJsonSchema,
        disableCache: true
      });
    }
    return {
      ok: false,
      provider: PROVIDERS.GEMINI,
      model,
      status: res.status,
      code: "API_ERROR",
      message: redactSensitiveText(msg),
      cacheHint
    };
  }

  const text = extractGeminiText(json);
  const sources = extractGeminiSources(json);
  const usage = extractGeminiUsage(json);
  let cacheStorageTokenHours = 0;

  if (cacheHint === "gemini_cached_content" && cachedContentKey && usage?.cachedInputTokens > 0) {
    const entry = GEMINI_CACHED_CONTENT.get(cachedContentKey);
    const alreadyCharged = entry?.chargedTokenHours && entry.chargedTokenHours > 0;
    if (!alreadyCharged) {
      const createdAtMs = Number(entry?.createdAtMs) || 0;
      const expiresAtMs = Number(entry?.expiresAtMs) || 0;
      let ttlMs = 0;
      if (createdAtMs > 0 && expiresAtMs > createdAtMs) {
        ttlMs = expiresAtMs - createdAtMs;
      } else {
        ttlMs = GEMINI_CACHED_CONTENT_TTL_MS;
      }
      if (ttlMs > 0) {
        const ttlHours = ttlMs / 3600000;
        cacheStorageTokenHours = Math.max(0, Number(usage.cachedInputTokens) || 0) * ttlHours;
        if (entry && cacheStorageTokenHours > 0) {
          entry.chargedTokenHours = cacheStorageTokenHours;
          GEMINI_CACHED_CONTENT.set(cachedContentKey, entry);
        }
      }
    }
  }

  if (!isNonEmptyString(text)) {
    return {
      ok: false,
      provider: PROVIDERS.GEMINI,
      model,
      code: "EMPTY_RESPONSE",
      message: "Rponse vide de Gemini.",
      cacheHint
    };
  }

  return {
    ok: true,
    provider: PROVIDERS.GEMINI,
    model,
    text: text.trim(),
    sources,
    usage,
    cacheHint,
    cacheStorageTokenHours: cacheStorageTokenHours || 0
  };
}

function extractGeminiText(json) {
  try {
    const c = json?.candidates?.[0];
    const parts = c?.content?.parts;
    if (Array.isArray(parts)) {
      return parts.map((p) => p?.text || "").join("");
    }
    return "";
  } catch {
    return "";
  }
}

function extractGeminiError(json) {
  try {
    const msg = json?.error?.message;
    return msg ? String(msg) : "";
  } catch {
    return "";
  }
}

function extractGeminiErrorInfo(json) {
  try {
    const err = json?.error;
    if (!err) return null;
    const msg = err?.message;
    const statusCode = Number(err?.code);
    return {
      message: msg ? String(msg) : "",
      status: Number.isFinite(statusCode) ? statusCode : undefined
    };
  } catch {
    return null;
  }
}

function extractGeminiSources(json) {
  try {
    const c = json?.candidates?.[0];
    const md = c?.groundingMetadata;
    const chunks = md?.groundingChunks;
    if (!Array.isArray(chunks)) return [];

    const out = [];
    for (const ch of chunks) {
      const web = ch?.web;
      if (!web?.uri) continue;
      out.push({ title: web?.title || "", url: web.uri });
    }

    return dedupeSources(out);
  } catch {
    return [];
  }
}

// ---- Provider: OpenAI (Responses API) ----

async function callOpenAI({
  apiKey,
  model,
  system,
  user,
  maxOutputTokens,
  reasoningEffort,
  timeoutMs,
  webSearch,
  responseMimeType,
  responseJsonSchema
}) {
  const url = "https://api.openai.com/v1/responses";

  const body = {
    model,
    input: user, // Responses API uses 'input' (string or array of objects)
    max_output_tokens: maxOutputTokens,
    store: false
  };

  if (isNonEmptyString(reasoningEffort)) {
    body.reasoning = { effort: reasoningEffort };
  }

  // System prompt
  if (isNonEmptyString(system)) {
    const sysText = (responseMimeType === "application/json" || responseJsonSchema)
      ? system + " You must return valid JSON."
      : system;
    body.instructions = sysText;
  }

  // Structured output (JSON)
  // - If a JSON schema is provided: use json_schema strict mode.
  // - Else if responseMimeType is JSON: request a json_object.
  if (responseJsonSchema) {
    // For GPT-5 Mini / v1/responses, we try to use the most standard way if possible.
    // If body.text format fails, we rely on the prompt.
    // We kept the previous implementation but added prompt reinforcement.
    body.text = {
      format: {
        type: "json_schema",
        name: "excel_ai_schema",
        strict: true,
        schema: responseJsonSchema
      }
    };
  } else if (responseMimeType === "application/json") {
    body.text = { format: { type: "json_object" } };
  }

  // Web search tool
  if (webSearch) {
    body.tools = [{ type: "web_search" }];
    body.include = ["web_search_call.action.sources"];
  }

  const res = await fetchWithTimeout(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${apiKey}`
    },
    body: JSON.stringify(body)
  }, timeoutMs);

  const json = await safeJson(res);

  if (!res.ok) {
    const msg = extractOpenAIError(json) || `OpenAI API error (${res.status}).`;
    return { ok: false, provider: PROVIDERS.OPENAI, model, code: "API_ERROR", message: redactSensitiveText(msg) };
  }

  const text = extractOpenAIText(json);
  const sources = webSearch ? extractOpenAISources(json) : [];
  const usage = extractOpenAIUsage(json);

  if (!isNonEmptyString(text)) {
    return {
      ok: false,
      provider: PROVIDERS.OPENAI,
      model,
      code: "EMPTY_RESPONSE",
      message: "Rponse vide d'OpenAI."
    };
  }

  return { ok: true, provider: PROVIDERS.OPENAI, model, text: text.trim(), sources, usage };
}

function extractOpenAIText(json) {
  // 1. Try direct output_text (common in simple Responses)
  try {
    if (typeof json?.output_text === "string" && json.output_text.trim()) {
      return json.output_text;
    }
  } catch {
    // ignore
  }

  // 2. Try iterating over 'output' items (complex/agentic responses)
  try {
    const out = json?.output;
    if (Array.isArray(out)) {
      const texts = [];
      for (const item of out) {
        if (typeof item?.text === "string" && item.text.trim()) texts.push(item.text);
        if (typeof item?.output_text === "string" && item.output_text.trim()) texts.push(item.output_text);
        if (item?.type === "output_text" && typeof item?.text === "string") texts.push(item.text);
        if (item?.type === "text" && typeof item?.text === "string") texts.push(item.text);

        // Standard message output
        if (item?.type === "message") {
          const content = item?.content;
          if (Array.isArray(content)) {
            for (const part of content) {
              if (part?.type === "text" && part?.text) texts.push(part.text);
              if (part?.type === "output_text" && part?.text) texts.push(part.text);
            }
          } else if (typeof content === "string") {
            texts.push(content);
          }
        }

        if (item?.type !== "message") {
          const content = item?.content;
          if (Array.isArray(content)) {
            for (const part of content) {
              if (part?.type === "text" && part?.text) texts.push(part.text);
              if (part?.type === "output_text" && part?.text) texts.push(part.text);
            }
          } else if (typeof content === "string") {
            texts.push(content);
          }
        }
      }
      if (texts.length > 0) return texts.join("\n").trim();
    }
  } catch {
    // ignore
  }

  // 3. Fallback: Chat Completions style (just in case model acts differently)
  try {
    return json?.choices?.[0]?.message?.content || "";
  } catch {
    return "";
  }
}

function extractOpenAIError(json) {
  try {
    const msg = json?.error?.message;
    return msg ? String(msg) : "";
  } catch {
    return "";
  }
}

function extractOpenAISources(json) {
  try {
    const out = json?.output;
    if (!Array.isArray(out)) return [];

    const sources = [];
    for (const item of out) {
      if (item?.type !== "web_search_call") continue;
      const s = item?.action?.sources;
      if (!Array.isArray(s)) continue;
      for (const src of s) {
        if (!src?.url) continue;
        sources.push({ title: src?.title || "", url: src.url });
      }
    }

    return dedupeSources(sources);
  } catch {
    return [];
  }
}

function extractGeminiUsage(json) {
  try {
    const u = json?.usageMetadata;
    if (!u) return null;
    return {
      inputTokens: toInt(u?.promptTokenCount),
      outputTokens: toInt(u?.candidatesTokenCount),
      reasoningTokens: toInt(u?.thoughtsTokenCount),
      totalTokens: toInt(u?.totalTokenCount),
      cachedInputTokens: toInt(u?.cachedContentTokenCount)
    };
  } catch {
    return null;
  }
}

function extractOpenAIUsage(json) {
  try {
    const u = json?.usage;
    if (!u) return null;
    const rawOutputTokens = toInt(u?.output_tokens);
    const reasoningTokens = toInt(u?.output_tokens_details?.reasoning_tokens);
    const outputTokens = Math.max(0, rawOutputTokens - reasoningTokens);
    return {
      inputTokens: toInt(u?.input_tokens),
      outputTokens,
      reasoningTokens,
      totalTokens: toInt(u?.total_tokens),
      cachedInputTokens: toInt(u?.input_tokens_details?.cached_tokens)
    };
  } catch {
    return null;
  }
}

function normalizeUsage(usage) {
  if (!usage || typeof usage !== "object") {
    return { inputTokens: 0, outputTokens: 0, reasoningTokens: 0, totalTokens: 0, cachedInputTokens: 0 };
  }
  const inputTokens = toInt(usage.inputTokens);
  const outputTokens = toInt(usage.outputTokens);
  const reasoningTokens = toInt(usage.reasoningTokens);
  const cachedInputTokens = toInt(usage.cachedInputTokens);
  const reportedTotal = toInt(usage.totalTokens);
  const computedTotal = inputTokens + outputTokens + reasoningTokens;
  const totalTokens = Math.max(reportedTotal, computedTotal);
  return {
    inputTokens,
    outputTokens,
    reasoningTokens,
    totalTokens,
    cachedInputTokens
  };
}

function toInt(value) {
  const n = Number(value);
  if (!Number.isFinite(n) || n <= 0) return 0;
  return Math.floor(n);
}

export async function clearAllCaches() {
  CACHE.clear();
  await clearPersistentCache();
}

export async function listGeminiCachedContents(options = {}) {
  const apiKey = await getApiKey(PROVIDERS.GEMINI);
  if (!apiKey) {
    return { ok: false, code: "API_KEY_MISSING", message: "Cl API manquante pour Gemini." };
  }
  const pageSize = clampInt(options?.pageSize ?? 50, 1, 100, 50);
  const maxPages = clampInt(options?.maxPages ?? 3, 1, 5, 3);
  const allPages = !!options?.all;
  let pageToken = isNonEmptyString(options?.pageToken) ? String(options.pageToken) : "";
  let pages = 0;
  const caches = [];

  while (pages < maxPages) {
    pages += 1;
    const url = new URL("https://generativelanguage.googleapis.com/v1beta/cachedContents");
    url.searchParams.set("pageSize", String(pageSize));
    if (pageToken) url.searchParams.set("pageToken", pageToken);

    const res = await fetchWithTimeout(url.toString(), {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "x-goog-api-key": apiKey
      }
    }, 30000);

    const json = await safeJson(res);
    if (!res.ok) {
      const msg = extractGeminiError(json) || `Gemini API error (${res.status}).`;
      return { ok: false, status: res.status, code: "API_ERROR", message: redactSensitiveText(msg) };
    }

    const list = Array.isArray(json?.cachedContents)
      ? json.cachedContents
      : (Array.isArray(json?.cachedContent) ? json.cachedContent : []);
    caches.push(...list);

    pageToken = isNonEmptyString(json?.nextPageToken) ? String(json.nextPageToken) : "";
    if (!allPages || !pageToken) break;
  }

  return { ok: true, caches, nextPageToken: pageToken || "", pages };
}

export async function deleteGeminiCachedContent(name) {
  const apiKey = await getApiKey(PROVIDERS.GEMINI);
  if (!apiKey) {
    return { ok: false, code: "API_KEY_MISSING", message: "Cl API manquante pour Gemini." };
  }
  const path = String(name || "").trim();
  if (!path) return { ok: false, code: "INVALID_NAME", message: "Nom de cache invalide." };

  const url = `https://generativelanguage.googleapis.com/v1beta/${encodeURI(path)}`;
  const res = await fetchWithTimeout(url, {
    method: "DELETE",
    headers: {
      "Content-Type": "application/json",
      "x-goog-api-key": apiKey
    }
  }, 30000);

  const json = await safeJson(res);
  if (!res.ok) {
    const msg = extractGeminiError(json) || `Gemini API error (${res.status}).`;
    return { ok: false, status: res.status, code: "API_ERROR", message: redactSensitiveText(msg) };
  }

  for (const [key, entry] of GEMINI_CACHED_CONTENT.entries()) {
    if (entry?.name && entry.name === path) {
      GEMINI_CACHED_CONTENT.delete(key);
    }
  }

  return { ok: true };
}

function dedupeSources(list) {
  const seen = new Set();
  const out = [];
  for (const s of list || []) {
    const url = String(s?.url || "").trim();
    if (!url) continue;
    const key = url.toLowerCase();
    if (seen.has(key)) continue;
    seen.add(key);
    out.push({ title: String(s?.title || "").trim(), url });
  }
  return out;
}

// ---- Helpers: fetch + timeout + JSON parsing ----

async function fetchWithTimeout(url, options, timeoutMs) {
  const t = normalizeTimeoutMs(timeoutMs);

  if (typeof AbortController === "undefined") {
    // Basic fetch without cancellation (should be rare in modern Office runtimes)
    return await fetch(url, options);
  }

  const ctrl = new AbortController();
  const id = setTimeout(() => ctrl.abort(), t);

  try {
    return await fetch(url, { ...options, signal: ctrl.signal });
  } finally {
    clearTimeout(id);
  }
}

async function safeJson(res) {
  try {
    return await res.json();
  } catch {
    return {};
  }
}
